<script>
import i18n from "../../i18n.js";
import * as firebaseui from "firebaseui"
import firebase from "../../firebase.js";
import { setTimeout } from "timers";
import FormUpload from "./FormUpload.vue";
import AppData from "../../AppData";
export default {
    props: {
        level: Number,
        path: String,
        item: {
            default: {}
        }
    },
    name: "PortfolioForm",
    data() {
        return {
            metaExanded: false,
            taskExanded: false,
            researchExanded: false,
            deviceExanded: false,
            designExanded: false,
            previewExanded: false,
            uploadProgress: 0,
            i18n: i18n,
            selected: [], // Must be an array reference!
            show: true,
            data: undefined,
            tagsList: [],
            expanded: undefined
        };
    },
    created() {
        AppData.fetchData("portfolio")
            .then(result => {
                console.log(result.tags.list);
                this.tagsList = result.tags.list;
            })
            .catch(err => {
                console.log(err);
            });

        this.expanded = [];
        if (this.item.hasOwnProperty("length")) {
            for (let i = 0; i < this.item.length; i++) {
                this.expanded.push(false);
            }
        } else {
            for (let key in this.item) {
                this.expanded.push(false);
            }
        }
        this.data = this.item;
    },
    methods: {
        expand(index) {
            this.expanded[index] = true;
            console.log(this.expanded, index);
            this.expanded = [...this.expanded];
        },
        collapse(index) {
            this.expanded[index] = false;
            this.expanded = [...this.expanded];
        },
        addElementToList(list) {
            console.log(list);
            if (list.length > 0) {
                if (typeof list[0] === "string") {
                    list.push("");
                } else {
                    var copy = JSON.parse(JSON.stringify(list[0])); // { ...list[0] }; //Object.assign({}, list[0]);
                    list.push(copy);
                }
            }
        },
        removeElementFromList(item, index) {
            console.log(item, index);
            item.splice(index, 1);
        },
        addElement() {},
        click() {
            // do nothing
        },
        uploadFile(e, id) {
            var input = document.getElementById(id);
            const file = input.files[0];
            if (file) {
                console.log(file);

                const name = +new Date() + "-" + file.name;
                const metadata = { contentType: file.type };
                const ref = firebase.storage().ref();
                const task = ref.child("uploads/" + name).put(file, metadata);
                task.then(snapshot => snapshot.ref.getDownloadURL())
                    .then(url => {
                        console.log(url);
                        var inputField = document.getElementById(
                            id.replace("file", "form")
                        );
                        inputField.value = url;
                        setTimeout(() => {
                            this.uploadProgress = 0;
                        }, 1000);
                    })
                    .catch(err => {
                        console.log(err);
                    });
                task.on("state_changed", snapshot => {
                    this.uploadProgress =
                        (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                    console.log(this.uploadProgress);
                });
            }
        }
    },
    components: { FormUpload }
};
</script>

