import firebase from "./firebase";

export default {
  fetchData(collection, returnArray = false, orderBy) {
    return new Promise(resolve => {
      const db = firebase.firestore();
      const settings = {};
      db.settings(settings);
      let ref = db.collection(collection);
      if (orderBy) {
        ref = ref.orderBy(orderBy.sort, orderBy.order);
      }
      ref
        .get()
        .then(result => {
          console.log(result.docs);
          var data = {};
          if (returnArray) {
            data = result.docs.map(element => element.data());
          } else {
            data = {};
            result.docs.forEach(element => {
              data[element.id] = element.data();
            });
          }
          console.log(data);
          resolve(data);
        })
        .catch(() => {
          resolve(this[collection]);
        });
    });
  },
  removeData(collection, doc) {
    const db = firebase.firestore();
    const settings = {};
    db.settings(settings);
    db.collection(collection)
      .doc(doc)
      .delete()
      .then(result => {
        console.log(result);
      })
      .catch(err => {
        console.log(err);
      });
  },
  saveData(collection, doc, value) {
    return new Promise((resolve, reject) => {
      const db = firebase.firestore();
      const settings = {};
      db.settings(settings);
      db.collection(collection)
        .doc(doc)
        .set(value)
        .then(result => {
          console.log(result);
          resolve(result)
        })
        .catch(err => {
          console.log(err);
          reject(err)
        });
    })
  },
  uploadAppData() {
    const db = firebase.firestore();
    const settings = {};
    db.settings(settings);
    let collections = [
      "navigation",
      "main",
      "coworking",
      "office",
      "business",
      "realty",
      "contacts",
      "event",
      "callback",
      "footer"
    ];
    collections.forEach(element => {
      var data = this[element];
      console.log(this[element]);
      console.log(element);
      for (let key in data) {
        if (Array.isArray(data[key])) {
          db.collection(element)
            .doc(key)
            .set({ list: data[key] })
            .then(result => {
              console.log(result);
            })
            .catch(err => {
              console.log(err);
            });
        } else {
          db.collection(element)
            .doc(key)
            .set(data[key])
            .then(result => {
              console.log(result);
            })
            .catch(err => {
              console.log(err);
            });
        }
      }
    });
  },
  cached: {}
};
