<template>
    <b-form-group :label="label" :label-cols="5" class="col-12">
        <div class="list-item">
            <b-form-input
                ref="formInput"
                type="text"
                autocomplete="name"
                :value="value"
                :v-model="value"
                @input="onInput"
                @change="onChange"
            ></b-form-input>
        </div>
        <div class="list-item">
            <b-form-file :plain="true" ref="fileInput" @change="uploadFile"></b-form-file>
            <b-progress
                style="justify-self:stretch"
                v-if="uploadProgress !== 0"
                :value="uploadProgress"
                :max="100"
                show-progress
                animated
            ></b-progress>
            <div v-if="uploaded" style="text-align:right" >Загружено</div>
            <!-- <b-button
                type="upload"
                size="sm"
                variant="primary"
                @click.prevent="(e)=>{uploadFile(e);}"
            >
                <i class="fa fa-upload"></i> Загрузить
            </b-button> -->
        </div>
    </b-form-group>
</template>

<script>
import * as firebaseui from "firebaseui"
import firebase from "../../firebase.js";
export default {
    name: "FormUpload",
    props: {
        label: String,
        value: String
    },
    data() {
        return {
            uploaded:false,
            uploadProgress: 0,
            newValue: ""
        };
    },
    created() {
        this.newValue = this.value;
    },
    methods: {
        onInput(value) {
            console.log(value);
            this.newValue = value;
            this.$emit("input", this.newValue);
        },
        onChange(value) {
            this.newValue = value;
            this.$emit("change", this.newValue);
        },
        uploadFile() {
            this.uploaded = false;
            var input = this.$refs.fileInput.$el;
            const file = input.files[0];
            if (file) {
                console.log(file);

                const name = +new Date() + "-" + file.name;
                const metadata = { contentType: file.type };
                const ref = firebase.storage().ref();
                const task = ref.child("uploads/" + name).put(file, metadata);
                task.then(snapshot => snapshot.ref.getDownloadURL())
                    .then(url => {
                        // setTimeout(() => {
                        //     this.uploadProgress = 0;
                        // }, 1000);
                        this.uploaded = true;
                        this.uploadProgress = 0;
                        this.newValue = url;
                        this.$emit("input", this.newValue);
                    })
                    .catch(err => {
                        console.log(err);
                    });
                task.on("state_changed", snapshot => {
                    this.uploadProgress =
                        (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
                    console.log(this.uploadProgress);
                });
            }
        }
    }
};
</script>

