<template>
    <b-col md="12" :key="k">
        <template v-if="data">
            <b-card
                v-for="(item, key, index) in data"
                :key="index"
                :class="[{'collapsed':!expanded[index]}]"
            >
                <div slot="header">
                    <strong>
                        <a href="/" v-if="expanded[index]" @click.prevent="() => {collapse(index)}">
                            -
                            <span>{{i18n.get(key)}}</span>
                        </a>
                        <a href="/" v-else @click.prevent="() => {expand(index)}">
                            +
                            <span>{{i18n.get(key)}}</span>
                        </a>
                    </strong>
                    <b-button
                        style="margin-left:20px"
                        type="submit"
                        size="sm"
                        variant="primary btn-circle"
                        v-if="addBlockButton"
                        @click="()=>{addClickHandler()}"
                    >+</b-button>
                     <b-button
                        style="margin-left:20px"
                        type="submit"
                        size="sm"
                        variant="danger btn-circle"
                        v-if="index > 0"
                        @click="()=>{removeClickHandler(key, index)}"
                    >-</b-button>
                </div>
                <b-form
                    @submit.prevent="() => submitHandler(index, key)"
                    :id="'form_' + key"
                    v-show="expanded[index]"
                >
                    <PortfolioFormElement ref="portfolioItems" :item="item"></PortfolioFormElement>
                    <div slot="footer" class="block-footer" ref>
                        <b-button type="submit" size="sm" variant="primary">
                            <div v-if="saving"><i class="fa fa-spinner fa-pulse"></i> Сохраняем</div>
                            <div v-else-if="!saving && !saved"><i class="fa fa-dot-circle-o"></i> Сохранить</div>
                            <div v-else><i class="fa fa-check"></i>  Сохранено</div>
                        </b-button>
                    </div>
                </b-form>
            </b-card>
        </template>
    </b-col>
</template>

<script>
import PortfolioFormElement from "./PortfolioFormElement.vue";
import AppData from "../../AppData.js";
import i18n from "../../i18n.js";
import firebase from "../../firebase.js";
import { setTimeout } from "timers";
export default {
    props: {
        useCustomTemplate: Boolean,
        blockTitle: {
            default: ""
        },
        addBlockButton: Boolean,
        collection: String
    },
    data() {
        return {
            k: 0,
            firebase: firebase,
            modal: false,
            i18n: i18n,
            data: undefined,
            expanded: undefined,
            saving:false,
            saved:false,
            timer:0,
        };
    },
    created() {
        this.expanded = [];
        if (this.data != undefined) {
            if (this.data.hasOwnProperty("length")) {
                for (let i = 0; i < this.data.length; i++) {
                    this.expanded.push(false);
                }
            } else {
                for (let key in this.data) {
                    this.expanded.push(false);
                }
            }
        }
    },
    mounted() {
        firebase.auth().onAuthStateChanged(user => {
            if (user) {
                AppData.fetchData(this.collection)
                    .then(result => {
                        this.data = result;
                        console.log("result = ", result);
                    })
                    .catch(err => {
                        console.log(err);
                    });
            } else {
                this.$router.push("/pages/login/");
                // Implement logic to trigger the login dialog here or redirect to sign-in page.
                // e.g. showDialog()
            }
        });
    },
    methods: {
        expand(index) {
            this.expanded[index] = true;
            console.log(this.expanded, index);
            this.expanded = [...this.expanded];
        },
        collapse(index) {
            this.expanded[index] = false;
            this.expanded = [...this.expanded];
        },
        addClickHandler() {
            var i = 1;
            var template;
            while (this.data[this.blockTitle + i] !== undefined) {
                template = this.data[this.blockTitle + i];
                i++;
            }
            this.data[this.blockTitle + i] = Object.assign({}, template);
            this.data[this.blockTitle + i].id = i;

            this.data = this.data;
            AppData.saveData(
                this.collection,
                this.blockTitle + i,
                this.data[this.blockTitle + i]
            ).then(()=> {
                setTimeout(()=> {
                    this.saving = false;
                }, 500)
            }).catch(()=>{
                setTimeout(()=> {
                    this.saving = false;
                }, 500)
            })
            this.k++;
        },
        removeClickHandler(key, index) {
            delete this.data[key];
            this.data = this.data;
            AppData.removeData(
                this.collection,
                key
            );
            this.k++;
        },

        
        showAuth() {
            this.modal = true;
        },
        submitHandler(index, key) {
            this.saving = true
            var doc = key;
            
            this.data[doc] = {
                ...this.$refs.portfolioItems[index].item
            };
            this.data[doc].alias = this.data[doc].preview.href.replace("/portfolio/", "")
            console.log(this.$refs.portfolioItems[index].item);
            // Array.from(e.target.elements).forEach(element => {
            //     var data = this.data;
            //     if (element.id) {
            //         console.log(element.id);
            //         let objectPath = element.id
            //             .replace(/[_]/g, ".")
            //             .replace(/\.(\d{1,2})/g, "[$1]");
            //         console.log(objectPath);
            //         if (element.id.indexOf("file-input") !== -1) {
            //             console.log(element.checked);
            //         } else if (element.type === "checkbox") {
            //             console.log(element.checked);
            //             eval("data." + objectPath + "= element.checked");
            //         } else {
            //             console.log(element.value);
            //             eval("data." + objectPath + "= element.value");
            //         }
            //     }
            // });
            clearTimeout(this.timer)
            console.log(this.collection, doc, this.data[doc])
            AppData.saveData(this.collection, doc, this.data[doc]).then(()=> {
                this.saving = false;
                this.saved = true;
                clearTimeout(this.timer)
                this.timer = setTimeout(()=> {
                    this.saving = false;
                    this.saved = false;
                }, 5000)
            }).catch(()=>{
                this.saving = false;
                clearTimeout(this.timer)
                this.timer = setTimeout(()=> {
                    this.saving = false;
                    this.saved = false;
                }, 5000)
            })
        }
    },
    components: {
        PortfolioFormElement
    }
};
</script>

<style lang="scss">
.collapsed {
    .card-body {
        display: none;
    }
}
a {
    color: #000;
    &:hover {
        text-decoration: none;
    }
}
.block-footer {
    margin-top: 20px;
}
.gly-spin {
  -webkit-animation: spin 2s infinite linear;
  -moz-animation: spin 2s infinite linear;
  -o-animation: spin 2s infinite linear;
  animation: spin 2s infinite linear;
}

@-moz-keyframes spin {
  0% {
    -moz-transform: rotate(0deg);
  }
  100% {
    -moz-transform: rotate(359deg);
  }
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
  }
}
@-o-keyframes spin {
  0% {
    -o-transform: rotate(0deg);
  }
  100% {
    -o-transform: rotate(359deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
</style>